import { DEFAULT_CONFIG, ConfigOptions } from '../core/config.ts'
import { MATRIX_OPTIONS, NUMBER_OPTIONS } from '../core/function/config.ts'

// create a read-only version of config
export const config = function (options?: Partial<ConfigOptions>): Readonly<ConfigOptions> {
  if (options) {
    throw new Error(
      'The global config is readonly. \n' +
        'Please create a mathjs instance if you want to change the default configuration. \n' +
        'Example:\n' +
        '\n' +
        "  import { create, all } from 'mathjs';\n" +
        '  const mathjs = create(all);\n' +
        "  mathjs.config({ number: 'BigNumber' });\n"
    )
  }

  return Object.freeze(DEFAULT_CONFIG)
}
Object.assign(config, DEFAULT_CONFIG, { MATRIX_OPTIONS, NUMBER_OPTIONS })
